export default function StyledSVG({ rotation, color }) {
  const styleSvg = {
    fill: "none",
    stroke: { color },
    strokeWidth: "4",
    transform: { rotation }
  };

  const svg = (
    <svg height="100" width="100">
      <polyline points="10,10,90,10,90,90" style={styleSvg} />
    </svg>
  );

  return svg;
}
